import { pipe, toPairs, map, flatten, sortBy, prop, find, propEq } from 'ramda';

const getTwitchEmoteSrc = (id) =>
  `https://static-cdn.jtvnw.net/emoticons/v1/${id}/1.0`;
const getTwitchEmoteSrcSet = (id) =>
  `https://static-cdn.jtvnw.net/emoticons/v1/${id}/1.0 1x, https://static-cdn.jtvnw.net/emoticons/v1/${id}/2.0 2x, https://static-cdn.jtvnw.net/emoticons/v1/${id}/3.0 4x`;

// twitch-emotes, bttv-emotes, ffz-emotes, emoji, links, mentions
const transformEmotesFromTags = pipe(
  toPairs,
  map(([id, value]) => map((v) => ({ id, ...v }), value)),
  flatten,
  sortBy(prop('start')),
);

const formatMessage = ({ message, tags, ...rest }, { twitch, bttv, ffz }) => {
  const { emotes } = tags;
  let messageArray = [];
  const isOwnMessage = emotes === undefined;

  if (!isOwnMessage && Object.keys(emotes).length > 0) {
    const emotesFromTags = transformEmotesFromTags(emotes);
    let offset = 0;
    emotesFromTags.forEach(({ id, start, end }, index) => {
      const textBefore = message.substring(offset, start);

      if (textBefore) messageArray.push(textBefore);

      const emote = message.substring(start, end + 1);

      messageArray.push({
        type: 'twitch-emote',
        text: emote,
        src: getTwitchEmoteSrc(id),
        srcSet: getTwitchEmoteSrcSet(id),
      });

      offset = end + 1;

      const isLast = index === emotesFromTags.length - 1;

      if (isLast) {
        const textAfter = message.substring(offset, message.length);
        if (textAfter) messageArray.push(textAfter);
      }
    });
  } else if (isOwnMessage) {
    messageArray = message.split(' ').map((word) => {
      const emote = find(propEq('code', word))(twitch);
      return emote
        ? {
            type: 'twitch-emote',
            text: word,
            src: getTwitchEmoteSrc(emote.id),
            srcSet: getTwitchEmoteSrcSet(emote.id),
          }
        : `${word} `;
    });
  } else {
    messageArray.push(message);
  }

  return {
    ...rest,
    tags,
    message,
    messageArray,
  };
};

export default formatMessage;
